import styled from 'styled-components'
import Img from 'gatsby-image'
import media from 'styled-media-query'

export const RedeSocialAvatarWrapper = styled(Img)`

    display: inline-block;
    width: 32px;
    margin-top: 30px;
    margin-left: 15px;

    ${media.lessThan('huge')` 
        width: 32px;
    `}

    
    ${media.lessThan('large')` 
        width: 32px;
    `}

`